AAA;IACA,aAAA;CACA;AACA;IACA,oBAAA;CACA;AACA;IACA,YAAA;IACA,kBAAA;IACA,gBAAA;IACA,kBAAA;IACA,aAAA;CACA;AACA;IACA,YAAA;IACA,aAAA;CACA;AACA;IACA,WAAA;IACA,WAAA;CACA;AACA;IACA,cAAA;IACA,kBAAA;IACA,WAAA;IACA,YAAA;IACA,mDAAA;IACA,SAAA;IACA,SAAA;CACA;AACA;IACA,cAAA;IACA,kBAAA;IACA,WAAA;IACA,YAAA;IACA,2DAAA;IACA,SAAA;IACA,UAAA;CACA;AACA;IACA,kBAAA;;IAEA,MAAA;IACA,aAAA;CACA;AACA;IACA,kBAAA;IACA,WAAA;IACA,cAAA;IACA,YAAA;IACA,iBAAA;IACA,eAAA;IACA,QAAA;CACA;AACA;IACA,qBAAA;IACA,UAAA;CACA;AACA;IACA,SAAA;CACA;AACA;IACA,UAAA;CACA;AAIA;IACA,WAAA;IACA,YAAA;IACA,WAAA;IACA,iBAAA;CACA;AACA;IACA,aAAA;IACA,oBAAA;IACA,8DAAA;CACA;AACA;IACA,YAAA;IACA,YAAA;CACA;AACA;IACA,WAAA;IACA,cAAA;IACA,mBAAA;IACA,+BAAA;CACA;AACA;IACA,cAAA;CACA;AACA;IACA,yBAAA;IACA,WAAA;IACA,gBAAA;IACA,kBAAA;IACA,kBAAA;IACA,qBAAA;IACA,YAAA;IACA,iBAAA;IACA,WAAA;IACA,eAAA;IACA,mBAAA;IACA,UAAA;CACA;AACA;IACA,iBAAA;IACA,mBAAA;IACA,aAAA;CACA;AACA;IACA,iBAAA;CACA;AACA;IACA,wBAAA;CACA;AACA;IACA,qBAAA;IACA,WAAA;IACA,UAAA;CACA;AACA;IACA,cAAA;IACA,kBAAA;IACA,UAAA;CACA;AACA;IACA,yBAAA;IACA,WAAA;IACA,qBAAA;IACA,YAAA;IACA,iBAAA;IACA,kBAAA;IACA,cAAA;IACA,kBAAA;IACA,gBAAA;IACA,qBAAA;CACA;AACA;IACA,WAAA;IACA,kBAAA;IACA,eAAA;IACA,eAAA;CACA;AACA;IACA,kBAAA;IACA,QAAA;CACA;AACA;IACA,mBAAA;IACA,kBAAA;IACA,SAAA;IACA,cAAA;CACA;AACA;IACA,kBAAA;CACA;AACA;IACA,mBAAA;IACA,QAAA;CACA;AACA;IACA,qBAAA;IACA,sBAAA;IACA,gBAAA;CACA;AACA;IACA,WAAA;IACA,yBAAA;CACA;AACA;IACA,aAAA;CACA;AACA;IACA,4BAAA;CACA;AACA;IACA,YAAA;CACA;AACA;IACA,aAAA;CACA;AACA;IACA,oBAAA;CACA;AACA;IACA,WAAA;IACA,gBAAA;CACA;AACA;IACA,cAAA;CACA;AACA;IACA,cAAA;IACA,QAAA;CACA;AACA;IACA,eAAA;IACA,kBAAA;IACA,iBAAA;IACA,mBAAA;IACA,gBAAA;CACA;AACA;IACA,WAAA;IACA,WAAA;IACA,cAAA;CACA;AAGA;IACA,kBAAA;CACA;AACA;IACA,WAAA;IACA,YAAA;IACA,YAAA;IACA,gBAAA;IACA,gBAAA;CACA;AACA;IACA,WAAA;IACA,cAAA;IACA,WAAA;IACA,YAAA;IACA,sBAAA;IACA,sBAAA;IACA,kBAAA;IACA,eAAA;IACA,gBAAA;IACA,iBAAA;IACA,WAAA;IACA,cAAA;CACA;AACA;IACA,gBAAA;CACA;AACA;IACA,qBAAA;CACA;AACA;IACA,WAAA;IACA,cAAA;IACA,WAAA;IACA,YAAA;IACA,cAAA;IACA,iBAAA;IACA,SAAA;IACA,0BAAA;IACA,6BAAA;IACA,kBAAA;IACA,eAAA;IACA,qBAAA;CACA;AACA;IACA,gBAAA;IACA,WAAA;IACA,eAAA;IACA,iBAAA;IACA,UAAA;CACA;AACA;IACA,YAAA;CACA;AAGA;IACA,iBAAA;CACA;AACA;IACA,eAAA;IACA,sBAAA;CACA;AACA;IACA,iBAAA;IACA,cAAA;CACA;AACA;IACA,mBAAA;IACA,gBAAA;CACA;AACA;IACA,yBAAA;IACA,eAAA;IACA,WAAA;IACA,qBAAA;IACA,qBAAA;IACA,qBAAA;CACA;AACA;IACA,qBAAA;IACA,iBAAA;IACA,YAAA;IACA,iBAAA;KC/QI,iBDgRJ;IACA,yBAAA;IACA,kBAAA;IACA,eAAA;IACA,WAAA;IACA,yBAAA;IACA,gBAAA;IACA,qBAAA;CACA;AACA;IACA,qBAAA;IACA,WAAA;IACA,kBAAA;CACA;AACA;IACA,qBAAA;CACA;AACA;IACA,qBAAA;IACA,iBAAA;IACA,YAAA;IACA,iBAAA;IACA,yBAAA;IACA,kBAAA;IACA,eAAA;IACA,WAAA;IACA,yBAAA;IAGA,kDAAA;IACA,2BAAA;IACA,qBAAA;CACA;AACA;IACA,qBAAA;IACA,WAAA;IACA,kBAAA;CACA","file":"productDetail.vue","sourcesContent":["<template>\n    <div>\n       <current-loc></current-loc>\n        <!--商品详情页上方-->\n        <div class=\"detail cle z-detail-box\">\n            <div class=\"detail_wrap\">\n                <div class=\"detail_img\" id=\"detail_img\">\n                    <div class=\"pic_view\">\n                        <a class=\"MagicZoomPlus\" id=\"Zoomer\" style=\"position: relative; display: inline-block; text-decoration: none; outline: 0px; overflow: hidden; width: auto; height: auto;\">\n                            <img :src=\"curShow.image\" alt=\"\" style=\"opacity: 1;\">\n                            <div class=\"MagicZoomPup\" style=\"z-index: 10; position: absolute; overflow: hidden;  visibility: hidden; width: 190px; height: 190px; opacity: 0.5;\"></div>\n                            <div class=\"MagicZoomPlusHint\" style=\"display: block; overflow: hidden; position: absolute; visibility: visible; z-index: 1; left: 2px; right: auto; top: 2px; bottom: auto; opacity: 0.75; max-width: 376px;\"></div>\n                        </a>\n                    </div>\n                    <div class=\"item-thumbs\" id=\"item-thumbs\">\n                        <a class=\"prev\" href=\"javascript:void(0);\" @click=\"ImagePrev\"></a>\n                        <a class=\"next\" href=\"javascript:void(0);\" @click=\"ImageNext\"></a>\n                        <div class=\"bd\">\n                            <div class=\"tempWrap\" style=\"overflow:hidden; position:relative; width:330px\">\n                                <ul class=\"cle\" style=\"width: 330px; position: relative; overflow: hidden; padding: 0px; margin: 0px; left: 0px;\">\n                                    <li v-for=\"(item,index) in proDetail.images\" :class=\"{'current': index===curIndex}\" @click=\"replaceShow(index,item)\">\n                                        <a>\n                                            <img :src=\"item.image\" alt=\"\">\n                                        </a>\n                                    </li>\n                                </ul>\n                            </div>\n                        </div>\n\n                    </div>\n                </div>\n                <div class=\"item-info\" id=\"item-info\">\n                    <dl class=\"loaded\">\n                        <dt class=\"product_name\">\n                        <h1>{{proDetail.name}}</h1>\n                        <p class=\"desc\"> <span class=\"gray\">{{proDetail.goods_brief}}</span> </p>\n                        </dt>\n                        <dd class=\"property\">\n                            <ul>\n                                <li v-if=\"proDetail.ship_free\">此商品为免运费商品，计算配送金额时将不计入配送费用 </li>\n                                <li>\n                                    <span class=\"lbl\">市场价</span> <em class=\"cancel\">￥{{proDetail.market_price}}元</em></li>\n                                <li>\n                                    <span class=\"icon_promo\">抢购</span> <span class=\"lbl\">促销价</span><span class=\"unit\"> <strong class=\"nala_price red\" id=\"ECS_SHOPPRICE\">￥{{proDetail.shop_price}}元</strong> </span>  <span class=\"timedown\" id=\"timedown\"></span>\n                                </li>\n                                <li>\n                                    <span class=\"lbl\">销&nbsp;&nbsp;&nbsp;量</span><span>最近售出<em class=\"red\">{{proDetail.sold_num}}</em>件</span></li>\n                            </ul>\n                        </dd>\n                        <dd class=\"tobuy-box cle\">\n                            <ul class=\"sku\">\n                                <li class=\"skunum_li cle\">\n                                    <span class=\"lbl\">数&nbsp;&nbsp;&nbsp;量</span>\n                                    <div class=\"skunum\" id=\"skunum\"> <span class=\"minus\" title=\"减少1个数量\" @click=\"reduceNum\"><i class=\"iconfont\">-</i></span>\n                                        <input id=\"number\" name=\"number\" type=\"text\" min=\"1\" v-model=\"buyNum\"  onchange=\"countNum(0)\">\n                                        <span class=\"add\" title=\"增加1个数量\" @click=\"addNum\"><i class=\"iconfont\">+</i></span> <cite class=\"storage\"> 件 </cite>\n                                    </div>\n                                    <div class=\"skunum\" id=\"skunum\">\n\n                                        <cite class=\"storage\">(<font id=\"shows_number\">{{proDetail.goods_num}}件</font>)</cite>\n\n                                    </div>\n                                </li>\n                                <li class=\"add_cart_li\">\n                                    <a class=\"btn\" id=\"buy_btn\" @click=\"addShoppingCart\">\n                                        <i class=\"iconfont\">&#xe600;</i>\n                                        加入购物车</a>\n\n                                    <a v-if=\"hasFav\" id=\"fav-btn\" class=\"graybtn\" @click=\"deleteCollect\">\n                                        <i class=\"iconfont\">&#xe613;</i>已收藏</a>\n                                  <a v-else class=\"graybtn\" @click=\"addCollect\">\n                                    <i class=\"iconfont\">&#xe613;</i>收藏</a>\n                                </li>\n                            </ul>\n                        </dd>\n                    </dl>\n                </div>\n            </div>\n\n        </div>\n\n        <!--商品详情页下方-->\n        <div class=\"detail_bgcolor\">\n            <div class=\"z-detail-box cle\">\n                <div class=\"z-detail-left\">\n                    <div class=\"taocan_bd\">\n                        <div class=\"product_tabs\">\n                            <div class=\"sectionbox z-box\" id=\"spxqitem\">\n                                <div class=\"spxq_main\">\n                                    <div>\n                                        <div>\n                                            <table>\n                                                <tbody>\n                                                <tr>\n                                                    <td width=\"20%\" class=\"th\"> 产品名称 :</td>\n                                                    <td width=\"80%\"> {{proDetail.name}}</td>\n                                                </tr>\n                                                </tbody>\n                                            </table>\n                                        </div>\n                                        <p>&nbsp; </p>\n                                    </div>\n                                    <div class=\"spxq_dec\">\n                                        <p v-html=\"proDetail.goods_desc\">\n                                        </p>\n                                    </div>\n                                </div>\n                            </div>\n                        </div>\n                    </div>\n                </div>\n                <!-- 热卖商品 -->\n                <hot-sales></hot-sales>\n            </div>\n        </div>\n        <model ref=\"model\"></model>\n    </div>\n</template>\n<script>\nimport cookie from '../../static/js/cookie';\nimport currentLoc from './current-loc/current-loc';\nimport hotSales from './hotSales';\nimport model from './model'\nimport { mapGetters } from 'vuex';\nimport { getGoodsDetail, getFav, addFav, delFav, addShopCart,getShopCart } from '../../api/api';\n  export default {\n    data () {\n        return {\n            productId: '', //当前商品id\n            curIndex: 1, //当前选中的图片序号\n            curShow: { //当前显示的图片\n                desc: '第一张细节图',\n                src: './images/1(1).jpg'\n            },\n            hasFav: false,\n            proDetail: {\n            },\n            buyNum:1\n        };\n    },\n    components: {\n        'current-loc': currentLoc,\n        'hot-sales': hotSales,\n         model\n    },\n    props: {\n\n    },\n    created () {\n        this.productId = this.$route.params.productId;\n        var productId = this.productId\n        if(cookie.getCookie('token')){\n          getFav(productId).then((response)=> {\n            this.hasFav = true\n          }).catch(function (error) {\n            console.log(error);\n          });\n        }\n        this.getDetails();\n    },\n    watch: {\n\n    },\n    computed: {\n    },\n    methods: {\n        getDetails () { //  请求商品详情\n          getGoodsDetail(this.productId)\n            .then((response)=> {\n                console.log(response.data);\n                this.proDetail = response.data;\n                this.curShow = this.proDetail.images[0];\n            }).catch(function (error) {\n                console.log(error);\n            });\n        },\n        // 减少数量\n        reduceNum () {\n            this.buyNum = this.buyNum === 1 ? 1 : this.buyNum - 1;\n        },\n\n        hasFaved () {\n\n        },\n\n        // 增加数量\n        addNum () {\n            this.buyNum = this.buyNum + 1;\n\n        },\n        addShoppingCart () { //加入购物车\n            addShopCart({\n                goods: this.productId, // 商品id\n                nums: this.buyNum, // 购买数量\n            }).then((response)=> {\n                this.$refs.model.setShow();\n                // 更新store数据\n                this.$store.dispatch('setShopList');\n\n            }).catch(function (error) {\n                console.log(error);\n            });\n\n            // this.$http.post('/shopcart/', {\n            //     params: {\n            //         goods: this.productId, // 商品id\n            //         nums: this.proDetail.purNum, // 购买数量\n            //     }\n            // }).then((response)=> {\n            //     console.log(response.data);\n            //     alert('已成功加入购物车');\n            //     // 更新store数据\n            //     this.$store.dispatch('setShopList');\n\n            // }).catch(function (error) {\n            //     console.log(error);\n            // });\n        },\n        addCollect () { //加入收藏\n          addFav({\n              goods: this.productId\n          }).then((response)=> {\n                console.log(response.data);\n                this.hasFav = true\n                alert('已成功加入收藏夹');\n            }).catch(function (error) {\n                console.log(error);\n            });\n        },\n\n        deleteCollect () {\n            //删除收藏\n          delFav(this.productId).then((response)=> {\n            console.log(response.data);\n            this.hasFav = false\n          }).catch(function (error) {\n            console.log(error);\n          });\n        },\n        ImagePrev () {\n            this.curIndex = this.curIndex === 0 ? 0 : this.curIndex - 1;\n        },\n        ImageNext () {\n            this.curIndex = this.curIndex === this.proDetail.detailImages.length - 1 ? this.proDetail.detailImages.length - 1 : this.curIndex + 1;\n        },\n        replaceShow (index, item) {\n            this.curIndex = index;\n            this.curShow = item;\n        }\n    }\n}\n</script>\n<style >\n\n\n.tabs_bar_warp {\n    height:48px;\n    width:970px\n}\n\n.tabs_bar_warp {\n    height:48px;\n    width:970px\n}\n.tabs_bar {\n    height:48px;\n    width:968px;\n    background:#f3f3f3;\n    border:1px solid #ccc;\n    z-index:999;\n    top:0\n}\n.tabs_bar ul {\n    width:100%;\n    height:48px\n}\n.tabs_bar ul li {\n    _bottom:-1px;\n    z-index:999;\n    position:relative;\n    display:block;\n    float:left;\n    height:48px;\n    line-height:48px\n}\n.tabs_bar ul li a {\n    border-bottom:1px solid #ccc;\n    border-right:1px solid #ccc;\n    padding:0 35px;\n    display:block\n}\n.tabs_bar ul li a:hover {\n    text-decoration:none;\n    background:#fff\n}\n.tabs_bar ul li.current_select a {\n    text-decoration:none;\n    background:#fff;\n    border-bottom:1px solid #fff\n}\n.tabs_bar ul li a em {\n    color:#09c762;\n    font-weight:bold\n}\n.tabs_bar li.tab-buy {\n    color:#09c762;\n    float:right;\n    padding-right:94px;\n    position:relative;\n    text-align:right;\n    width:200px;\n    margin-right:0;\n    visibility:hidden\n}\n.tabs_bar li.tab-buy strong {\n    font:24px/48px arial;\n    padding:0 4px\n}\n.tabs_bar li.tab-buy a.btn {\n    position:absolute;\n    right:8px;\n    top:6px;\n    font-size:16px;\n    height:23px;\n    line-height:22px;\n    padding:5px 12px;\n    background:#09c762\n}\n.tabs_bar li.tab-buy a.btn:hover {\n    background:#1e9246\n}\n.tabs_bar li.tab-buy a.graybtn {\n    position:absolute;\n    right:8px;\n    top:8px;\n    height:30px;\n    line-height:30px;\n    cursor:default;\n    background:#e5e5e5;\n    padding:0 12px;\n    border-color:#ddd;\n    box-shadow:none\n}\n.tabs_bar li.tab-buy a.graybtn:hover {\n    background:#e5e5e5\n}\n/*.sectionbox h2.tit {*/\n    /*background:url(images/spxq_nav_bg.png) 0 0 no-repeat;*/\n    /*height:27px;*/\n    /*padding:0 15px;*/\n    /*margin-bottom:10px*/\n/*}*/\n/*.sectionbox h2.tit span {*/\n    /*background:url(images/spxq_nav_bg_t.png) -9999px 0 no-repeat;*/\n    /*display:block;*/\n    /*height:0;*/\n    /*padding-top:27px;*/\n    /*overflow:hidden*/\n/*}*/\n/*.sectionbox h2.tit1 span {*/\n    /*background-position:0 -52px*/\n/*}*/\n/*.sectionbox h2.tit2 span {*/\n    /*b