exports.push([module.id, "\n.banner-warp{\n  height:300px;\n}\n", "", {"version":3,"sources":["/Users/tunny/Profile/Code/Python/online-store-master/src/views/index/src/views/index/banners.vue"],"names":[],"mappings":";AAWA;EACA,aAAA;CACA","file":"banners.vue","sourcesContent":["<template>\n  <div class=\"banner-warp\">\n    <swiper :options=\"swiperOption\">\n        <swiper-slide v-for=\"item in banners\" :key=\"item.goods\">\n          <router-link :to=\"'/app/home/productDetail/'+item.goods\" target = _blank> <img :src=\"item.image\" alt=\"\" /></router-link>\n        </swiper-slide>\n        <div class=\"swiper-pagination\" slot=\"pagination\"></div>\n    </swiper>\n  </div>\n</template>\n<style>\n  .banner-warp{\n    height:300px;\n  }\n</style>\n\n\n\n<script>\n  import {bannerGoods} from '../../api/api'\n\n  export default {\n    data() {\n\n      return {\n\n        swiperOption: {\n          pagination: '.swiper-pagination',\n          paginationClickable: true,\n          autoplay: 2500,\n          autoplayDisableOnInteraction: false,\n        },\n        banners:[]\n\n      }\n\n    },\n    methods:{\n      getBanner(){\n        bannerGoods()\n          .then((response)=> {\n            console.log(response)\n            // 跳转到首页页response.body面\n            this.banners = response.data\n          })\n          .catch(function (error) {\n            console.log(error);\n          });\n      }\n    },\n    created(){\n      this.getBanner();\n    }\n\n  }\n\n</script>\n"],"sourceRoot":""}]);

