Error\">{{parseWordError}}</p>\n     <!--        <div class=\"error btns login-form-tips\" id=\"jsLoginTips\" v-show=\"error\"><p>用户名或密码错误</p></div> -->\n            <div class=\"auto-box marb38\">\n            </div>\n            <p class=\"error-text\" v-show=\"error\">{{error}}</p>\n            <input class=\"btn btn-green\" id=\"jsLoginBtn\" type=\"button\" @click = \"login\" value=\"立即登录 &gt; \">\n          </form>\n          <ul class=\"form other-form\">\n            <li>\n              <h5>使用第三方帐号登录</h5>\n            </li>\n            <li class=\"other-login\">\n              <a class=\"qq\" href=\"http://115.159.122.64:8000/login/qq/\"></a>\n              <a class=\"sina\" href=\"http://115.159.122.64:8000/login/weibo/\"></a>\n              <a class=\"weixin\" href=\"http://115.159.122.64:8000/login/weixin/\"></a>\n            </li>\n          </ul>\n          <p class=\"form-p\">\n            没有帐号？\n            <router-link :to=\"'/app/register/'\" target = _blank>[立即注册]</router-link>\n          </p>\n        </div>\n\n      </div>\n    </div>\n  </div>\n</template>\n<script>\n  import cookie from '../../static/js/cookie';\n  import { login } from '../../api/api'\n  export default {\n    data(){\n      return {\n        userName:'',\n        parseWord:'',\n        autoLogin:false,\n        error:false,\n        userNameError:'',\n        parseWordError:''\n      }\n    },\n    methods:{\n      login(){\n        // if(this.userName==''||this.parseWord==''){\n        //   this.error = true;\n        //   return\n        // }\n        var that = this;\n      login({\n          username:this.userName, //当前页码\n          password:this.parseWord\n      }).then((response)=> {\n            console.log(response);\n            //本地存储用户信息\n            cookie.setCookie('name',this.userName,7);\n            cookie.setCookie('token',response.data.access,7)\n            //存储在store\n            // 更新store数据\n            that.$store.dispatch('setInfo');\n            //跳转到首页页面\n            this.$router.push({ name: 'index'})\n          })\n          .catch(function (error) {\n            if(\"non_field_errors\" in error){\n              that.error = error.non_field_errors[0];\n            }\n            if(\"username\" in error){\n              that.userNameError = error.username[0];\n            }\n            if(\"password\" in error){\n              that.parseWordError = error.password[0];\n            }\n          });\n\n        //      this.$http.post('/login', {\n        //   params: {\n        //     userName:this.userName,\n        //     parseWord:this.parseWord,\n        //   }\n        // })\n        //   .then((response)=> {\n        //     console.log(response);\n        //     //本地存储用户信息\n        //     cookie.setCookie('name',response.data.info.name,7);\n        //     cookie.setCookie('id',response.data.info.id,7)\n        //     //存储在store\n        //     // 更新store数据\n        //     this.$store.dispatch('setInfo');\n\n        //     //跳转到首页页面\n        //     this.$router.push({ name: 'index'})\n\n        //   })\n        //   .catch(function (error) {\n        //     console.log(error);\n        //   });\n      },\n      errorUnshow(){\n        this.error = false;\n      }\n    },\n    created(){\n      //清除缓存\n      cookie.delCookie('token');\n      cookie.delCookie('name');\n      //重新触发store\n      //更新store数据\n      this.$store.dispatch('setInfo');\n    }\n  }\n</script>\n<style  scoped>\n  .error-text{\n    color:#fa8341;\n  }\n  .other-form li {\n    padding-bottom: 8px;\n    margin-bottom: 10px;\n  }\n  .other-form li h5 {\n    font-size:12px;\n  }\n\n  .other-login a {\n    margin-top: 0;\n    vertical-align: top;\n    margin-right: 10px;\n    background: url(../../static/images/login/other-login-bg.png) center no-repeat;\n    display: inline-block;\n    width: 30px;\n    height: 30px;\n    overflow: hidden;\n  }\n  .other-login a.qq {\n    background-position: -40px 0;\n  }\n  .other-login a.sina {\n    back