-index:100;\n  }\n  .unslider-arrow.prev {\n    left: 0;\n    background:url(../../static/images/login/slide_l.png) no-repeat center center;\n  }\n  .unslider-arrow.prev:hover {\n    background:url(../../static/images/login/slide_l_1.png) no-repeat center center;\n  }\n  .unslider-arrow.next{\n    right:0;\n    background:url(../../static/images/login/slide_r.png) no-repeat center center;\n  }\n  .unslider-arrow.next:hover{\n    background:url(../../static/images/login/slide_r_1.png) no-repeat center center;\n  }\n  .hd-login > h1{\n    float:left;\n    color:#fff;\n    font-size:30px;\n    font-weight: normal;\n  }\n\n\n\n  .form-box .form-group.focus,\n  .form-box .valcode.error {\n    border-color: #6ec558;\n    box-shadow: 0 0 5px #6ec558;\n  }\n  .form-box .form-group.blur,\n  .form-box  .valcode.blur {\n    border-color: #ccc;\n  }\n  .form-box .form-group.errorput,\n  .form-box .valcode.errorput input {\n    border-color: #f00;\n    box-shadow: 0 0 5px #aa0b0b;\n  }\n  .form-box a{\n    color:#666;\n  }\n  .form-box a:hover{\n    color:#6ec559;\n  }\n  .form-box{\n    position:relative;\n    width:290px;\n    height: 472px;\n    padding:0 40px;\n    background:#fff;\n    color:#666;\n  }\n  .form-box > h2,\n  .form-box > .tab{\n    height:54px;\n    line-height:54px;\n    margin-bottom:34px;\n    font-size:18px;\n    font-weight:normal;\n    color:#333;\n    border-bottom:1px solid #eaeaea;\n  }\n  .form-box > .tab > h2{\n    float:left;\n    width:90px;\n    height:53px;\n    line-height:53px;\n    cursor: pointer;\n    font-weight:normal;\n    text-align:center;\n  }\n  .form-box > .tab > h2.active{\n    border-bottom:3px solid #6ec55a;\n    color:#333;\n  }\n\n\n  .form-group{\n    position:relative;\n    width:288px;\n    height:38px;\n    border:1px solid #dedede;\n    -webkit-border-radius: 3px;\n    -moz-border-radius: 3px;\n    border-radius: 3px;\n    overflow:hidden;\n  }\n  .form-group > label{\n    float:left;\n    width:72px;\n    height:20px;\n 